2: ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', ''],
<template>
	<div v-if="!showPuzzle" style="margin: auto; display: flex; flex-direction: column; gap: 10px; color: #fff">
		Puzzle maker
		<input v-model="gridSize" type="text" placeholder="Puzzle size" />
		<button @click="createPuzzle">Create Puzzle</button>
	</div>
	<div v-if="showPuzzle" style="width: 600px; height: 600px; background: #424d9a; margin: auto;cursor:pointer;">
		<div v-for="(row, index1) in grid" :key="row" style="width: 100%" :style="`height:${600 / grid[1].length}px`">
			<div
				v-for="(box, index2) in row"
				@click="movePiece(`${index1}-${index2}`)"
				:ref="(el) => (refs[`${index1}-${index2}`] = el)"
				:key="box"
				style="float: left"
				:style="`width:${600 / grid[1].length}px;height:${600 / grid[1].length}px`"
			></div>
		</div>
		<div
			v-for="(piece, index) in puzzlePositions"
			@click="handleSelectPiece(piece.pos)"
			:key="piece"
			class="transitions"
			:class="{ selected: selectedPiece === piece.pos }"
			style="
				display: flex;
				position: absolute;
				background: #6574e0;
				cursor: pointer;
				outline: 1px solid #505aa4;
				transition: top 1s ease, left 1s ease;
			"
			:style="`width:${600 / grid[1].length}px;height:${600 / grid[1].length}px;top:${piece.top}px;left:${piece.left}px`"
		>
			<div style="margin: auto; color: #fff" :style="`font-size:${600 / grid[1].length / 4}px`">
				{{ index + 2 }}
			</div>
		</div>
	</div>
</template>

<script>
import { ref, onMounted, nextTick } from 'vue';
export default {
	name: 'App',
	components: {},
	setup() {
		const grid = ref({});

		const refs = ref({});
		const puzzlePositions = ref([]);
		const selectedPiece = ref();
		const showPuzzle = ref(false);
		const gridSize = ref('');

		const handleSelectPiece = (key) => {
			selectedPiece.value = key;
		};

		const createPuzzle = () => {
			for (let i = 0; i < +gridSize.value; i++) {
				grid.value[i + 1] = Array(+gridSize.value).fill('');
			}

			grid.value[1][0] = null;

			showPuzzle.value = true;
			nextTick(() => {
				for (const key in refs.value) {
					const { offsetTop, offsetLeft } = refs.value[key];
					if (key != `1-0`) puzzlePositions.value.push({ pos: key, top: offsetTop, left: offsetLeft });
				}
			});
		};

		const movePiece = (key) => {
			const [selectedRow, selectedPieceIndex] = selectedPiece.value.split('-').map((n) => +n);
			const [selectedRowMove, selectedMoveIndex] = key.split('-').map((n) => +n);

			if (
				!(
					(selectedRow === selectedRowMove && selectedPieceIndex + 1 === selectedMoveIndex) ||
					(selectedRow === selectedRowMove && selectedPieceIndex - 1 === selectedMoveIndex) ||
					(selectedRow + 1 === selectedRowMove && selectedPieceIndex === selectedMoveIndex) ||
					(selectedRow - 1 === selectedRowMove && selectedPieceIndex === selectedMoveIndex)
				)
			) {
				return;
			}

			const index = puzzlePositions.value.findIndex((piece) => piece.pos === selectedPiece.value);
			const { offsetLeft, offsetTop } = refs.value[key];

			puzzlePositions.value[index].left = offsetLeft;
			puzzlePositions.value[index].top = offsetTop;
			puzzlePositions.value[index].pos = key;
		};

		onMounted(() => {
			nextTick(() => {
				for (const key in refs.value) {
					const { offsetTop, offsetLeft } = refs.value[key];
					if (key != `1-0`) puzzlePositions.value.push({ pos: key, top: offsetTop, left: offsetLeft });
				}
				console.log(puzzlePositions);
			});
		});

		return {
			grid,
			gridSize,
			createPuzzle,
			handleSelectPiece,
			showPuzzle,
			movePiece,
			selectedPiece,
			puzzlePositions,
			refs,
		};
	},
};
</script>

<style>
#app {
	font-family: Avenir, Helvetica, Arial, sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	text-align: center;
	height: 100vh;
	display: flex;
	color: #2c3e50;
}

body {
	margin: 0;
	background: #121633;
}

* {
	box-sizing: border-box;
}

.selected {
	outline: 2px solid rgb(8, 8, 8) !important;
	z-index: 999;
}
</style>
